<template>
	<div class="page1_2_5">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="审计标准及依据" title="知识图谱" id="more1_2_4" ydname="审计准备"/>
			</div>
		</div>
		<div>
			<div class="cont box-border-shadow">
	            <div id="graphCharts" style="width: 100%;height: 800px;margin: 0 auto"></div>
	        </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import * as echarts from 'echarts'
	import reqData from "../../../reqData.js"
	export default{
		name:'page1_2_4',
		data(){
			return{
				msg:["审计准备","审计标准及依据","知识图谱"],
				jsonData:{}
			}
		},
		components:{
			Place,
			interBtn
		},
		computed: {
		    
	    },
	    methods:{
	    	unique(arr,links){
		   　　　var newArr = [arr[0].name];
		   		var data=[arr[0]];
		  　　 	for(var i=1;i<arr.length;i++){
		  			// console.log(arr[i]+'=========='+newArr.indexOf(arr[i]));
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
        		}
        		console.log(data);
		        return data;
		   },
		   getData(){
		   	 reqData
			        .req({
			          apiName: "Neo4jToJson"
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.nodes
						let links=data.links;
						nodes=this.unique(nodes);
						// links=this.linksFormat(links);
						var graphCharts = echarts.init(document.getElementById('graphCharts'));
				    	graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:'auto',
								height:'auto'
							})
					    })

			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
		   graphOption(nodes,links){
		    	let option = {
				     title: {
				         text: '知识图谱'
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 58,
				         roam: true,
				         edgeSymbol: ['circle', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         edgeLabel: {
				             normal: {
				                 textStyle: {
				                     fontSize: 20
				                 }
				             }
				         },
				         force: {
				             initLayout:'circular',
				             repulsion: 1500,
            				 // layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [10, 50]
				         },
				         draggable: true,
				         itemStyle: {
				             normal: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2
				             }
				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 18,color:'#000'},
				                 formatter: function(x) {
				                     return x.data.name;
				                 }
				             }
				         },
				         label: {
				             normal: {
				                 show: true,
				                 // textStyle: {fontSize: 18}
				             }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		    }
	    },
	    mounted(){
	    	this.getData();
	    	
	    }
	}
</script>
<style type="text/css" scoped="">
	.cont{
		text-align: center; 
		margin: 20px auto;
		min-height: 800px;
	     background-color: #fff;
	    padding: 10px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
</style>


